import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar, Button} from "@vkontakte/vkui";
import {} from "@vkontakte/icons";
import {Icon28Play} from "@vkontakte/icons";
import {decOfNum} from "../../js/utils";

export class Main extends PureComponent {

    render() {
        const
            {t} = this.props,
            {lvl, pictures, successfulPictures, guessedPictures} = t.state.user,
            {gamesAsWinner, gamesAsDrawer, top_number, snackbar} = t.state,
            offset = [Math.min(t.state.colors.length - 3, Math.max(lvl, 4) - 4), Math.min(t.state.colors.length, Math.max(lvl, 3) + 3)],
            colors = t.state.colors.slice(offset[0], offset[1])
        ;

        return (
            <div className='Main'>
                <Avatar src={t.state.vk_user.photo_100} size={48} shadow={false}/>
                <h3>Привет, {t.state.vk_user.first_name}</h3>
                <h1>Твой профиль</h1>
                <div className='Colors__Container'>
                    <div className='Gradient_Opacity'/>
                    {
                        colors.map((value, index) =>
                            <div
                                className='Color'
                                key={`Color__${index}`}
                                style={{
                                    marginLeft: index > 0 && 12,
                                    background: index + 1 + offset[0] === lvl && 'linear-gradient(156.04deg, #F05F88 0%, #FBD20A 100%)',
                                    border: index + 1 + offset[0] === lvl && 'none'
                                }}
                            >
                                <span>{index + 1 + offset[0]}</span>
                                <div style={{background: value}}/>
                            </div>
                        )
                    }
                </div>
                <p>Отгадывай и рисуй картины, чтобы разблокировать новые цвета</p>
                <div className='Cards__Container'>
                    <div onClick={() => {
                        if (gamesAsDrawer.length > 0) {
                            t.go('gallery')
                        }
                    }}>
                        <span
                            className='Top'>{decOfNum(successfulPictures, ['успешная', 'успешные', 'успешных'])}</span>
                        <div className='Images'>
                            {[1, 0, 2].map((value, index) => <div num={index} key={`div__${index}`}>{
                                gamesAsDrawer[value] && <img alt='img' src={gamesAsDrawer[value]['Picture.url']}/>
                            }</div>)}
                        </div>
                        <div className='Texts'>
                            <span>Галерея</span>
                            <span>{decOfNum(pictures, ['картина', 'картины', 'картин'])}</span>
                        </div>
                    </div>

                    <div onClick={() => t.go('rating')}>
                        <span className='Top'>{top_number && `Ты на ${top_number} месте`}</span>
                        <div className='Images'>
                            {[1, 0, 2].map((value, index) => <div num={index} key={`div__${index}`}>{
                                gamesAsWinner[value] && <img alt='img' src={gamesAsWinner[value]['Picture.url']}/>
                            }</div>)}
                        </div>
                        <div className='Texts'>
                            <span>Отгадано</span>
                            <span>{decOfNum(guessedPictures, ['картина', 'картины', 'картин'])}</span>
                        </div>
                    </div>
                </div>
                <div className='ButtonPlay' onClick={() => t.go('search')}>
                    <Icon28Play/>
                </div>
            </div>
        )
    }
}

Main.defaultProps = {};

Main.propTypes = {
    t: PropTypes.object
};

export default Main;