import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar, Button, IconButton, Input, ScreenSpinner, WriteBarIcon} from "@vkontakte/vkui";
import {Icon28ChevronRightOutline, Icon28ReportOutline, Icon28ShareOutline} from "@vkontakte/icons";
import bridge from "@vkontakte/vk-bridge";
import {decOfNum, getSrcUrl, getUrlParams} from "../../js/utils";
import {vk_local_users} from "../../js/drawerapp/utils";

export class Result extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            isDrawer: props.t.state.drawerId === props.t.state.vk_user.id,
            isWinner: props.t.state.winnerId === props.t.state.vk_user.id,
            winnerId: props.t.state.winnerId,
            drawerId: props.t.state.drawerId,
            word: props.t.state.word
        };

        this.shareStory = this.shareStory.bind(this);
    }

    componentDidMount() {

    }

    shareStory() {
        this.props.t.setPopout(<ScreenSpinner/>);
        const
            {createCanvas, loadImage} = require('canvas'),
            {isDrawer, winnerId, isWinner} = this.state,
            type = isDrawer ? 1 : isWinner ? 2 : 3
        ;

        loadImage(getSrcUrl(require(`../../assets/drawing/stories/Story${type}.png`))).then(async background => {
            const
                canvas = createCanvas(background.width, background.height),
                ctx = canvas.getContext('2d'),
                img = new Image(),
                {word} = this.state,
                title = word.nom.substring(0, 1).toUpperCase() + word.nom.substring(1)
            ;
            ctx.drawImage(background, 0, 0);

            if (type === 1) {
                ctx.font = '64px TT Commons Demibold';
                ctx.textAlign = 'center';
                ctx.fillStyle = '#616161';
                ctx.fillText(title, 539, 708 + 13 + 489);
            }

            img.src = this.props.t.state.canvas_;
            ctx.drawImage(img, 198, 618, 683, 683);

            try {
                await bridge.send('VKWebAppShowStoryBox', {
                    background_type: 'image',
                    blob: canvas.toDataURL('image/png'),
                    attachment: {
                        url: 'https://vk.com/app' + getUrlParams().vk_app_id,
                        text: 'go_to',
                        type: 'url'
                    }
                });
                this.props.t.socket.call('games.share', {gameId: this.props.t.state.gameId});
            } catch (e) {
            }
            this.props.t.updateData();
            this.props.t.setPopout(null);
            this.props.t.setActivePanel('main');
        });
    }

    render() {
        const
            {t} = this.props,
            {isDrawer, isWinner, winnerId, drawerId, word, reported} = this.state,
            time = Math.round((t.state.gameEnd - t.state.gameStart) / 1000)
        ;

        return (
            <div className='Result'>
                {
                    isDrawer ?
                        winnerId !== 0 ?
                            <React.Fragment>
                                <h1>Ты молодец!</h1>
                                <p>Твою картину угадали за {decOfNum(time, ['секунду', 'секунды', 'секунд'])}</p>
                            </React.Fragment>
                            :
                            <React.Fragment>
                                <h1>Время вышло</h1>
                                <p>Никто не угадал что нарисовано на твоей картине</p>
                            </React.Fragment>
                        :
                        winnerId !== 0 ?
                            isWinner ?
                                <React.Fragment>
                                    <h1>Ты молодец!</h1>
                                    <p>Ты первый угадал, что {vk_local_users[drawerId].first_name} нарисовал на
                                        картине</p>
                                </React.Fragment>
                                :
                                <React.Fragment>
                                    <h1>Игра окончена</h1>
                                    <p>{vk_local_users[winnerId].first_name} был первым, кто угадал, что изображено на
                                        картине</p>
                                </React.Fragment>
                            :
                            <React.Fragment>
                                <h1>Время вышло</h1>
                                <p>Никто не угадал что нарисовано на картине</p>
                            </React.Fragment>
                }
                <div className='Canvas_Container'>
                    {
                        (reported === false && isDrawer === false) &&
                        <Icon28ReportOutline className='Report' onClick={() => {
                            this.setState({reported: true});
                            t.socket.call('games.report', {game_id: t.state.gameId});
                        }}/>
                    }
                    <h2>{word.nom.substring(0, 1).toUpperCase() + word.nom.substring(1)}</h2>
                    <img alt='canvas' src={t.state.canvas_}/>
                    <Button
                        before={<Icon28ShareOutline width={16} height={16}/>}
                        size='m' mode='gradient'
                        onClick={() => {
                            this.shareStory();
                        }}
                    >
                        Поделиться
                    </Button>
                </div>
                <Button
                    size='m' mode='secondary'
                    onClick={() => {
                        t.updateData();
                        t.setActivePanel('main');
                    }}
                >
                    На главную
                </Button>
            </div>
        )
    }
}

Result.defaultProps = {};

Result.propTypes = {
    t: PropTypes.object
};

export default Result;